import React, { Component } from 'react'

class Languages extends Component {
    render() {
        let language=this.props.name;
            if(language === "java"){
                return(  <div>
                    <span className="lang java">‚Äç&nbsp;&nbsp;&nbsp;&nbsp;</span>
                    java
                    </div> )
            }else if(language === "cpp"){
                return( <div>
                    <span className="lang cpp">‚Äç&nbsp;&nbsp;&nbsp;&nbsp;</span>
                    cpp
                    </div> )
            }
            else if(language === "html"){
                return( <div>
                    <span className="lang html">‚Äç&nbsp;&nbsp;&nbsp;&nbsp;</span>
                    html
                    </div>)
            }
            else if(language === "css"){
                return(  <div>
                    <span className="lang css">‚Äç&nbsp;&nbsp;&nbsp;&nbsp;</span>
                    css
                    </div> )
            }
            else if(language === "php"){
                return(  <div>
                    <span className="lang php">‚Äç&nbsp;&nbsp;&nbsp;&nbsp;</span>
                    php
                    </div> )
            }
            else if(language === "csharp"){
                return(  <div>
                    <span className="lang csharp">‚Äç&nbsp;&nbsp;&nbsp;&nbsp;</span>
                    csharp
                    </div> )
            }else{
                return(<div></div>)
            }

        
    }
}

export default Languages
